.page {
  font-family: "Poppins", sans-serif;
  background-color: #1d1d1d;
  scroll-behavior: smooth;
  color: #fff;

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    // transition: transform 0.4s cubic-bezier(0.41, 0.49, 0.79, 3.03);
    transition: transform 0.5s ;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 80px;

    &-title {
      font-weight: 500;
      font-size: 32px;
      line-height: 32px;
      text-align: center;
      letter-spacing: -1.5px;
      text-transform: capitalize;
      margin-bottom: 48px;

      @include onTablet {
        font-size: 48px;

      }

      @include onDesktop {
        font-size: 48px;
      }
    }
  }
}
